/*头部菜单栏*/

.nav-global .nav-list {
    float: left;
    color :white;
    line-height: 28px;
    margin: 36px 23px 0 0;
}

.nav-global .nav-list li {
    float: left;
}

.nav-global .nav-list a {
    color: white ;
    display: inline-block;
    font-size: 14px;
    font-weight :600 ;
    padding: 0 25px;
}

.nav-global .nav-list a:hover {
    color: #fff;
}

.nav-aside {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: right;
    margin-top: 38px;
    border-left: 1px solid rgba(255, 255, 255, .2);
}

.nav-aside > li {
    position: relative;
    float: left;
}

.nav-aside > li > a {
    font-size: 0;
}

/*用户列表*/

.nav-user {
    margin-left: 40px;
    width: 36px;
}

.nav-user > a:before {
    content: " ";
    position: absolute;
    left: 8px;
    width: 20px;
    top: 0;
    height: 20px;
    background-size: 240px 107px;
    -webkit-transition: none;
    transition: none;
}

.nav-user:hover > a:before {
    background-position: -5px 0;
}

.nav-user-wrapper {
    position: absolute;
    z-index: 30;
    padding-top: 18px;
    opacity: 0;
    visibility: hidden;
    top: -3000px;
    width: 168px;
    left: 50%;
    margin-left: -70px;
}

.nav-user:hover .nav-user-wrapper {
    top: 18px;
    visibility: visible;
    opacity: 1;
    -webkit-transition: opacity .15s ease-out;
    transition: opacity .15s ease-out;
}

.nav-user-list {
    position: relative;
    width: 168px;
    padding-top: 20px;
    background: #fff;
    border: 1px solid #d6d6d6;
    border-color: rgba(0, 0, 0, .08);
    border-radius: 8px;
    -webkit-box-shadow: 0 20px 40px rgba(0, 0, 0, .15);
            box-shadow: 0 20px 40px rgba(0, 0, 0, .15)
}

.nav-user-list:before {
    position: absolute;
    content: " ";

    background-size: 240px 107px;
    width: 20px;
    height: 8px;
    left: 50%;
    top: -8px;
    margin-left: -25px;
}

.nav-user-avatar > dd {
    display: block;
    position: relative;
    margin: 0 auto;
    width: 46px;
    height: 46px;
    margin-bottom: 8px;
}

.nav-user-avatar > dd:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    border-radius: 50%;
    -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
            box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
}

.nav-user-avatar span {
    border-radius: 50%;
    display: block;
    width: 100%;
    height: 100%;

    background-size: contain;
}

.nav-user-avatar dt {
    margin-bottom: 16px;
    font-size: 12px;
    line-height: 1.5;
    text-align: center;
    color: #757575;
}

.nav-user-list li a {
    position: relative;
    border-top: 1px solid #f5f5f5;
    display: block;
    padding-left: 54px;
    line-height: 44px;
    height: 44px;
    color: #616161;
    font-size: 12px;
}

.nav-user-list li a:hover {
    background-color: #fafafa;
}

.nav-user-list li a:before {

    background-size: 240px 107px;
    content: ' ';
    display: block;
    position: absolute;
    left: 22px;
    top: 50%;
    width: 20px;
    height: 20px;
    margin-top: -8px;
}

.nav-user-list li.order a:before {
    background-position: 0 -44px;
}

.nav-user-list li.support a:before {
    background-position: -20px -44px;
}

.nav-user-list li.coupon a:before {
    background-position: -20px -84px;
}

.nav-user-list li.information a:before {
    background-position: 0 -64px;
}

.nav-user-list li.address a:before {
    background-position: -20px -64px;
}

.nav-user-list li.logout a:before {
    background-position: 0 -84px;
}

