    width: 360px;
}

.nav-cart-wrapper:before {
    position: absolute;
    content: ' ';

    background-size: 240px 107px;
    width: 20px;
    height: 8px;
    right: 34px;
    top: 10px;
    z-index: 11;
}

.nav-cart.active .nav-cart-wrapper {
    display: block;
