}

.nav-cart-list .nav-cart-items {
    max-height: 665px;
    overflow-x: hidden;
    overflow-y: auto;
}

.nav-cart-list .cart-item {
    height: 120px;
    width: 100%;
    overflow: hidden;
    border-top: 1px solid #f0f0f0;
}

.nav-cart-list .cart-item:first-child {
    border-top: none;
    border-radius: 8px 8px 0 0;
