    height: 80px;
    border-radius: 3px;
}

.nav-cart-list .item-thumb:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    border: 1px solid #f0f0f0;
    border: 0 solid transparent;
    -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
